:root {
    --s-loader-wrapper-bg-color: rgb(21, 22, 43);
    --s-loader-width: 50px;
    --s-loader-height: var(--s-loader-width);
    --s-loader-border-color-trans: rgba(230, 123, 44, 0.4);
    --s-loader-border-color-full: rgb(230, 123, 44);
  }

  div.container-spinning{
      width: 100%;
      display: -webkit-flex;
      display: flex;
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: center;
              justify-content: center;
  }
  
  .spinning-loader {
    width: 50px;
    width: var(--s-loader-width);
    height: 50px;
    height: var(--s-loader-height);
    border: 5px solid rgba(230, 123, 44, 0.4);
    border: 5px solid var(--s-loader-border-color-trans);
    border-left-color: rgb(230, 123, 44);
    border-left-color: var(--s-loader-border-color-full);
    border-radius: 50%;
    background: transparent;
    -webkit-animation-name: rotate-s-loader;
            animation-name: rotate-s-loader;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-duration: .6s;
            animation-duration: .6s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    position: relative;
  }
  
  @-webkit-keyframes rotate-s-loader {
    from {
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes rotate-s-loader {
    from {
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
div.gallery {
    display: grid;
    grid-gap: 25px;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    grid-auto-rows: 300px;
    padding: 1%;
    width: 100%;
    height: 100%;
  }

  div.gallery figure.width{
    width: 33%;
  }

  div.gallery figure:nth-child(4n+3) {
      grid-column: auto / span 2;
      grid-row: auto / span 2;
  }
  div.gallery figure:nth-child(4n+4) {
      grid-column: auto / span 2;
      grid-row: auto / span;
  }
  
  div.gallery figure{
      width: 100%;
      height: 100%;
      cursor: pointer;
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
      -webkit-flex-direction: column;
              flex-direction: column;
      font-size: 2.5em;
      color: #20263f;
      font-weight: 700;
      border-radius: 10px;
      z-index: 1000;
      transition: all .2s ease-in;
      position: relative;
  }
  div.gallery figure:hover {
      -webkit-transform: scale(1.05);
              transform: scale(1.05);
    }
    div.gallery figure:hover .description{
      display: block;
      position: absolute;
      text-align: center;
      top: 50%;
      background-color: transparent;
    }
    div.gallery figure:hover img{
      -webkit-filter: blur(4px) !important;
              filter: blur(4px) !important;
    }
  
    div.gallery figure span.description {
      display: block;
      position: absolute;
      padding: 4px 9px;
      line-height: 1.3 !important;
      background-color: rgba(56, 56, 56, .7);
      width: 100%;
      color: white;
      bottom: 0%;
      font-size: 0.6em;
      border-bottom-left-radius: 10px;
      border-bottom-right-radius: 10px;
  }
  
  
  div.gallery figure img.gallery__img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      -webkit-filter: drop-shadow(4px 4px 48px #cecece);
              filter: drop-shadow(4px 4px 48px #cecece);
      transition: all .2s ease-in;
      border-radius: 10px;
  }
