.Upload {
    display: flex;
    flex-direction: column;
    flex: 1;
    align-items: flex-start;
    text-align: left;
    overflow: hidden;
    -webkit-box-shadow: 1px 0px 12px -5px rgba(0,0,0,0.75);
    -moz-box-shadow: 1px 0px 12px -5px rgba(0,0,0,0.75);
    box-shadow: 1px 0px 12px -5px rgba(0,0,0,0.75);
    border-radius: 10px;
    padding: 1% 2%;
    margin-top: 1%;
    max-height: 110%;
  }
  
  .Content {
    display: flex;
    flex-direction: row;
    padding-top: 16px;
    box-sizing: border-box;
    width: 100%;
  }
  
  .Files {
    margin-left: 32px;
    align-items: flex-start;
    justify-items: flex-start;
    flex: 1;
    overflow-y: auto;
    max-height: 53vh;
  }
  
  .Actions {
    display: flex;
    flex: 1;
    width: 100%;
    align-items: flex-end;
    flex-direction: column;
    margin-top: 32px;
  }

  .titleUpload{
    display: flex;
    width: 100%;
  }
  
  .Title {
    margin-bottom: 32px;
    color: #555;
    width: 50%;
  }

  .visualizar{
    width: 50%;
    text-align: right;
    cursor: pointer;
  }
  
  .Filename {
    margin-bottom: 8px;
    font-size: 16px;
    color: #555;
  }
  
  .Row {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
    height: 50px;
    padding: 8px;
    overflow: hidden;
    box-sizing: border-box;
  }
  
  .CheckIcon {
    opacity: 0.5;
    margin-left: 32px;
  }
  
  .ProgressWrapper {
    display: flex;
    flex: 1;
    flex-direction: row;
    align-items: center;
  }
  
